import * as formValidation from "./formValidation.js";
import * as tableOperations from "./tableOperations.js";
import * as modalOperations from "./modalOperations.js";
import * as exportData from "./exportData.js";

const displayData = document.getElementById("displayData");
const exportDataButton = document.getElementById("exportData");
let table = null;

function showFormData() {
  // Fetching values from various input fields in the form
  const name = document.getElementById("name").value;
  const surname = document.getElementById("surname").value;
  const age = document.getElementById("age").value;
  const email = document.getElementById("email").value;
  const selectColor = document.getElementById("selectColor").value;
  const checkEmail = document.getElementById("checkEmail").checked;
  const checkPhone = document.getElementById("checkPhone").checked;
  const checkSMS = document.getElementById("checkSMS").checked;

  // Validating age, email, and contact preferences
  const validAge = Number(age) <= 120;
  const validEmail = formValidation.isValidEmail(email);
  const validContactPref = formValidation.validateContactPreferences(
    checkEmail,
    checkPhone,
    checkSMS
  );

  // Displaying error alerts if validation fails
  if (!validAge) {
    alert("Please enter a valid age (less than or equal to 120).");
    return;
  }
  if (!validEmail) {
    alert("Please enter a valid email address.");
    return;
  }
  if (!validContactPref) {
    alert("Please select at least one contact method.");
    return;
  }

  // Checking if there is any data entered
  const hasData =
    name ||
    surname ||
    age ||
    email ||
    selectColor ||
    checkEmail ||
    checkPhone ||
    checkSMS;

  // Displaying a message if there's no data entered
  if (!hasData) {
    displayData.innerHTML = "<p>No data to display.</p>";
    if (table) {
      table.remove();
      table = null;
    }
    return;
  }

  // Creating a table and inserting data into it
  if (!table) {
    const headers = [
      "Name",
      "Surname",
      "E-mail",
      "Age",
      "Selected Color",
      "Contact method",
      "Action",
    ];
    tableOperations.createTable(headers, displayData);
    table = displayData.querySelector("table");
  }

  const dataArray = [name, surname, email, age, selectColor];
  tableOperations.insertDataIntoTable(dataArray, {
    checkEmail,
    checkPhone,
    checkSMS,
  });

  // Clearing form fields after submission
  document.getElementById("name").value = "";
  document.getElementById("surname").value = "";
  document.getElementById("age").value = "";
  document.getElementById("email").value = "";
  document.getElementById("selectColor").value = "";
  document.getElementById("checkEmail").checked = false;
  document.getElementById("checkPhone").checked = false;
  document.getElementById("checkSMS").checked = false;
}

exportData.exportButton.onclick = function () {
  exportData.showExportModal(table);
};

document.getElementById("submitButton").addEventListener("click", showFormData);
